* {
  box-sizing: border-box;
}

.page {
  font-family: "PT Sans", "Arial", sans-serif;
}

.container {
  margin: 0 auto;
  height: 100%;
  width: 100%;
  max-width: 1200px;
}
@media (min-width: 320px) {
  .container {
    max-width: 767px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 1200px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.wrapper {
  box-shadow: 10px 0 19px -17px #000000, -10px 0 19px -17px #000000;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #FFFFFF;
}

body,
html {
  height: 100%;
  background-color: #F2F2F2;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
  margin: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
}

figure,
p {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 0;
  padding-block-end: 0;
  padding-inline-end: 0;
}

