@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsansbold.woff2") format "woff2", url("../fonts/ptsansbold.woff2") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.page-header {
  background-color: #FFFFFF;
}

.page-header__logo {
  position: absolute;
  left: calc(50% - 90px);
  width: 185px;
  height: 96px;
}

.nav-list {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 0;
  padding-block-end: 0;
  padding-inline-end: 0;
  width: 100%;
  display: none;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%) bottom/800px 10px no-repeat #81B3D2;
}
@media (min-width: 768px) {
  .nav-list {
    background-color: #FFFFFF;
    background-image: none;
    display: flex;
    justify-content: center;
  }
}

.nav-list__item {
  display: block;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  padding: 18px 0 17px 0;
  width: 20%;
  border-bottom: 1px solid #4886aa;
  border-top: 1px solid #bcd3e0;
}
@media (max-width: 767px) {
  .nav-list__item {
    width: 100%;
    height: 56px;
  }
}
.nav-list__item:nth-child(2) {
  margin-right: 10%;
}
.nav-list__item:nth-child(3) {
  margin-left: 10%;
}
@media (min-width: 768px) {
  .nav-list__item {
    border: none;
  }
}

.nav-list__link {
  text-decoration: none;
  color: #FFFFFF;
  transition: 0.4s;
}
@media (min-width: 768px) {
  .nav-list__link {
    color: #000000;
  }
}
.nav-list__link:active {
  opacity: 0.3;
  transition: 0.4s;
}
.nav-list__link:hover {
  text-decoration: none;
  color: #81B3D2;
  transition: 0.4s;
}
.nav-list__link--current {
  color: #766357;
}

@media (min-width: 320px) {
  .main-nav__burger {
    display: block;
    width: 62px;
    height: 56px;
    background: url("../img/header/icon-menu23x22.svg") center center no-repeat;
  }
}
@media (min-width: 768px) {
  .main-nav__burger {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.main-nav__burger:hover {
  opacity: 0.6;
  transition: opacity 0.4s;
}
.main-nav__burger:active {
  opacity: 0.3;
  transition: opacity 0.4s;
}

@media (max-width: 767px) {
  .main-nav--nojs .nav-list {
    position: static;
    display: flex;
    flex-direction: column;
    border-bottom: 56px #FFFFFF solid;
  }
  .main-nav--nojs .main-nav__burger {
    display: none;
  }
  .main-nav--nojs .nav-list__item {
    margin: 0;
  }
  .main-nav--nojs .page-header__logo {
    margin-top: 224px;
  }
}

.main-nav {
  min-height: 56px;
  display: flex;
  flex-direction: row-reverse;
}
@media (max-width: 767px) {
  .main-nav {
    width: 100%;
  }
}
.main-nav--opened .nav-list__item {
  margin: 0;
}
.main-nav--opened .nav-list {
  position: absolute;
  display: flex;
  flex-direction: column;
}
.main-nav--opened .main-nav__burger {
  position: absolute;
  background: url("../img/nav/icon-close.svg") center/22px 22px no-repeat rgba(255, 255, 255, 0);
}

